.header {
    background-color: #4CAF50;
    padding: 10px 0;
    color: white;
    text-align: center;
  }
  
  .header .logo h1 {
    margin: 0;
  }
  
  nav ul {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
  }
  
  nav ul li {
    margin: 0 15px;
  }
  
  nav ul li a {
    color: white;
    text-decoration: none;
  }
  