.product-page {
    text-align: center;
    padding: 20px;
  }
  
  .product-page img {
    width: 300px;
    height: auto;
    margin-bottom: 20px;
  }
  
  .related-product {
    margin: 10px 0;
  }
  
  .related-product button {
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .related-product button:hover {
    background-color: #45a049;
  }
  