body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  header {
    background-color: #4CAF50;
    padding: 10px 0;
    color: white;
    text-align: center;
  }
  
  nav ul {
    list-style: none;
    padding: 0;
  }
  
  nav ul li {
    display: inline;
    margin: 0 10px;
  }
  
  nav ul li a {
    color: white;
    text-decoration: none;
  }
  
  footer {
    background-color: #333;
    color: white;
    text-align: center;
    padding: 10px 0;
    position: fixed;
    bottom: 0;
    width: 100%;
  }
  
  main {
    padding: 20px;
  }
  
  .home {
    text-align: center;
  }
  
  .contact-form {
    width: 50%;
    margin: 0 auto;
  }
  
  .contact-form h2 {
    text-align: center;
  }
  
  .contact-form label {
    display: block;
    margin-bottom: 10px;
  }
  
  .contact-form input,
  .contact-form textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
  }
  
  .contact-form button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .contact-form button:hover {
    background-color: #45a049;
  }
  
  .service-page {
    text-align: center;
  }
  
  .service-section {
    margin-bottom: 20px;
  }
  
  .product-page {
    text-align: center;
  }
  
  .product-page img {
    width: 300px;
    height: auto;
  }
  
  .related-product {
    margin: 10px 0;
  }
  
  .related-product button {
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .related-product button:hover {
    background-color: #45a049;
  }
  